  pointer-events: initial;
  background-color: rgba(0, 0, 0, .2);
  transition: opacity .25s, visibility .25s, all cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

